import axios from 'axios';
import { signOut } from "next-auth/react";

async function fetchAutoToken() {
  try {
    const clientKey = process.env.NEXT_USER_KEY;
    const clientSecret = process.env.NEXT_USER_SECRET;
    const authHeader = btoa(`${clientKey}:${clientSecret}`);

    const res = await axios.post('/api/bridge/login', {}, {
      headers: {
        'X-ENDPOINT': '/auth/token',
        'X-METHOD': 'GET',
        'X-Timestamp': new Date().toISOString(), 
        'Authorization': `Basic ${authHeader}`
      }
    });
    return res.data?.data?.token || res.data?.token;
  } catch (err) {
    return null;
  }
}
export default async function postData(endpoint: string, data = {}, customHeader = {}) {
  try {
    let authHeaderValue = '';

    if (endpoint === '/auth/login') {
      const token = await fetchAutoToken();
      if (token) authHeaderValue = `Bearer ${token}`;
    }

    return await axios.post('/api/bridge/login', data, {
      headers: {
        'X-ENDPOINT': endpoint,
        'X-Timestamp': new Date().toISOString(),
        'X-Uniqueid': 'USR000001', 
        'Authorization': authHeaderValue,
        'X-Custom-Header': JSON.stringify({ 'X-Level': "1", ...customHeader }),
      }
    });
  } catch (error: any) {
    throw error;
  }
}